.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image{
  width : 80px;
  border : 2px solid white;
}
.imageTxt{
  width : 40px;
  border : 2px solid white;
}
.box{
  height : 550px;
}
.vertical{
  overflow-y: auto;
  background: linear-gradient(to right,rgb(134, 36, 1),rgb(255, 68, 0));
} 
.pointer{
  cursor : pointer;
  background-color: transparent;
  color : aliceblue;
}
.content{
  background: linear-gradient(to right,rgb(255, 68, 0),rgb(134, 36, 1));
  color : white;
}
.message_box{
  overflow-y: auto;
}
.message{
  display: flex;
  justify-content: flex-end;
  margin-top: 5px;
}
.head{
  background-color : rgb(134, 36, 1);
}
.header{
  background-color: rgba(105, 10, 10, 0.637);
  color :antiquewhite;
  font-size: 50px;
  font-weight: 700;
  text-align: center;
}